<template>
  <el-dialog
    :model-value="modelValue"
    @update:model-value="$emit('update:modelValue', $event)"
    title=""
    width="600px"
    :destroy-on-close="true"
  >
    <div class="submit-content">
      <h3></h3>
      <p class="description"></p>
      
      <el-form :model="form" label-width="120px" class="submit-form">
        <el-form-item label="" required>
          <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
        
        <el-form-item label="" required>
          <el-input v-model="form.url" placeholder=" http://  https:// "></el-input>
        </el-form-item>
        
        <el-form-item label="" required>
          <el-input
            v-model="form.description"
            type="textarea"
            :rows="3"
            placeholder=""
          ></el-input>
        </el-form-item>
        
        <el-form-item label="" required>
          <el-select v-model="form.category" placeholder="">
            <el-option label="AI" value="ai"></el-option>
            <el-option label="" value="dev"></el-option>
            <el-option label="" value="design"></el-option>
            <el-option label="" value="learn"></el-option>
            <el-option label="" value="productivity"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="form.email" placeholder=""></el-input>
        </el-form-item>
      </el-form>

      <div class="submit-footer">
        <el-button type="primary" @click="handleSubmit" :loading="submitting"></el-button>
        <el-button @click="$emit('update:modelValue', false)"></el-button>
      </div>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: 'RewardDialog',
  props: {
    modelValue: {
      type: Boolean,
      required: true
    }
  },
  emits: ['update:modelValue'],
  data() {
    return {
      form: {
        name: '',
        url: '',
        description: '',
        category: '',
        email: ''
      },
      submitting: false
    }
  },
  methods: {
    handleSubmit() {
      this.submitting = true;
      // 
      setTimeout(() => {
        this.submitting = false;
        // 
        this.$message({
          type: 'success',
          message: ''
        });
        // 
        this.$emit('update:modelValue', false);
        // 
        this.form = {
          name: '',
          url: '',
          description: '',
          category: '',
          email: ''
        };
      }, 1000);
    }
  }
}
</script>

