.submit-content {
  padding: 20px;
}

h3 {
  margin: 0 0 20px;
  text-align: center;
  color: var(--el-text-color-primary);
}

.description {
  text-align: center;
  color: var(--el-text-color-secondary);
  margin-bottom: 30px;
}

.submit-form {
  max-width: 500px;
  margin: 0 auto;
}

.submit-footer {
  text-align: center;
  margin-top: 30px;
}

