const pinyinMap = {
  '': 'a', '': 'a', '': 'a', '': 'a', '': 'a',
  '': 'b', '': 'b', '': 'b', '': 'b', '': 'b', '': 'b',
  '': 'c', '': 'c', '': 'c', '': 'c', '': 'c',
  '': 'd', '': 'd', '': 'd', '': 'd', '': 'd',
  '': 'e', '': 'e', '': 'e',
  '': 'f', '': 'f', '': 'f', '': 'f', '': 'f',
  '': 'g', '': 'g', '': 'g', '': 'g', '': 'g',
  '': 'h', '': 'h', '': 'h', '': 'h', '': 'h',
  '': 'j', '': 'j', '': 'j', '': 'j', '': 'j',
  '': 'k', '': 'k', '': 'k', '': 'k', '': 'k',
  '': 'l', '': 'l', '': 'l', '': 'l', '': 'l',
  '': 'm', '': 'm', '': 'm', '': 'm', '': 'm',
  '': 'n', '': 'n', '': 'n', '': 'n',
  '': 'o',
  '': 'p', '': 'p', '': 'p',
  '': 'q', '': 'q', '': 'q', '': 'q', '': 'q',
  '': 'r', '': 'r', '': 'r', '': 'r',
  '': 's', '': 's', '': 's', '': 's', '': 's',
  '': 't', '': 't', '': 't', '': 't', '': 't',
  '': 'w', '': 'w', '': 'w', '': 'w', '': 'w',
  '': 'x', '': 'x', '': 'x', '': 'x', '': 'x',
  '': 'y', '': 'y', '': 'y', '': 'y', '': 'y',
  '': 'z', '': 'z', '': 'z', '': 'z', '': 'z'
};

/**
 * 
 * @param {string} char 
 * @returns {string} 
 */
function getInitial(char) {
  return pinyinMap[char] || char;
}

/**
 * 
 * @param {string} str 
 * @returns {string} 
 */
export function getPinyinInitials(str) {
  return str.split('').map(char => getInitial(char)).join('');
}
